@import url(https://fonts.googleapis.com/css2?family=Rye&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,400;0,700,600;1,600&display=swap);
@media screen and (max-width: 377px){header{position:relative;width:100%;height:62px;background-color:#FFFBEB;display:flex;align-items:center;justify-content:center;box-shadow:2px -13px 30px -6px rgba(0,0,0,0.81);-webkit-box-shadow:2px -13px 30px -6px rgba(0,0,0,0.81);-moz-box-shadow:2px -13px 30px -6px rgba(0,0,0,0.81)}header h2{font-family:'Rye', cursive;font-size:1.5rem;color:#b45309}header a .arrow-back{position:absolute;left:7vw;width:16px;top:3.5vh}}

.input-section input{width:236px;height:42px;border-radius:6px;border:1px solid #9CA3AF;box-shadow:inset 0 0 4px #9CA3AF;background:transparent;padding:11px 16px 12px 16px}.input-section.search-input{margin-right:16px}

.btn{background-color:#B45309 !important;color:white !important;font-weight:700 !important;height:42px !important;padding-top:7px !important;box-shadow:1px -3px 21px -12px #B45309;-webkit-box-shadow:1px -3px 21px -12px #B45309;-moz-box-shadow:1px -3px 21px -12px #B45309}

.container{display:flex;justify-content:center}.container .result ul{font-family:'Open Sans', sans-serif;text-align:center;font-style:italic;font-weight:600;padding:0;margin:0 0 16px 0}.container.random-result{margin-top:24px}

@media screen and (max-width: 377px){section{font-family:'Open Sans', sans-serif;margin:24px 16px}section .input-wrapper{display:flex}section .input-wrapper.category{position:absolute;bottom:0;margin-bottom:16px}section .icon-wrapper{display:flex;justify-content:center;margin-top:32px}section .icon-wrapper .cn-icon{width:60px}section .random-button{display:flex;justify-content:center;margin-top:34px}section .list-of-category{position:absolute;background-color:white;width:236px;bottom:65px;left:16px;box-shadow:-3px 3px 20px -8px rgba(0,0,0,0.75);-webkit-box-shadow:-3px 3px 20px -8px rgba(0,0,0,0.75);-moz-box-shadow:-3px 3px 20px -8px rgba(0,0,0,0.75);padding-left:17px}section .list-of-category ul{font-weight:700;margin:5px 0;padding:0}}

@media screen and (max-width: 377px){.container{display:block;padding:0 !important}.container span{font-family:'Open Sans', sans-serif;color:#B45309;margin:0px auto;font-weight:600;font-size:1rem;margin-bottom:32px}.container .icon-wrapper{margin-bottom:16px;justify-content:center;margin-top:32px}.container .icon-wrapper .cn-icon{width:60px}}

